<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />
  </div>
</template>

<script>
export default {
  name: "App",
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isRouterAlive: true
    };
  },
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    }
  },
  components: {}
};
</script>

<style>
@import "@/../assets/comment";
#app {
  width: 100%;
  height: 100%;
}
.ivu-btn > .ivu-icon {
  line-height: 1;
  vertical-align: middle;
  font-size: 16px;
}
</style>



